"use client"

import { useState } from 'react'
import Header from '@/components/Header'
import Sidebar from '@/components/Sidebar'
import BookCard from '@/components/BookCard'
import BookFormModal from '@/components/BookFormModal'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'

interface Book {
  id: string
  title: string
  author: string
  cover: string
  synopsis: string
  status: 'lendo' | 'lido' | 'a-ler'
  rating: number
  shelf: string
}

interface Shelf {
  id: string
  name: string
}

export default function BooksPage() {
  const [books, setBooks] = useState<Book[]>([
    {
      id: '1',
      title: 'O Hobbit',
      author: 'J.R.R. Tolkien',
      cover: '',
      synopsis: 'A jornada de Bilbo Bolseiro em uma aventura inesperada.',
      status: 'lido',
      rating: 5,
      shelf: '2'
    },
    {
      id: '2',
      title: 'Sapiens',
      author: 'Yuval Noah Harari',
      cover: '',
      synopsis: 'Uma breve história da humanidade.',
      status: 'lendo',
      rating: 4,
      shelf: '3'
    },
    {
      id: '3',
      title: '1984',
      author: 'George Orwell',
      cover: '',
      synopsis: 'Um romance distópico sobre totalitarismo.',
      status: 'a-ler',
      rating: 0,
      shelf: '2'
    }
  ])

  const [shelves, setShelves] = useState<Shelf[]>([
    { id: '1', name: 'Favoritos' },
    { id: '2', name: 'Ficção' },
    { id: '3', name: 'Não-ficção' }
  ])

  const [selectedShelf, setSelectedShelf] = useState<string>('all')
  const [searchTerm, setSearchTerm] = useState('')
  const [statusFilter, setStatusFilter] = useState<string>('all')
  const [ratingFilter, setRatingFilter] = useState<string>('all')
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [editingBook, setEditingBook] = useState<Book | null>(null)

  const filteredBooks = books.filter(book => {
    const matchesSearch = book.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         book.author.toLowerCase().includes(searchTerm.toLowerCase())
    const matchesShelf = selectedShelf === 'all' || book.shelf === selectedShelf
    const matchesStatus = statusFilter === 'all' || book.status === statusFilter
    const matchesRating = ratingFilter === 'all' || 
                          (ratingFilter === '4+' && book.rating >= 4) ||
                          (ratingFilter === '3+' && book.rating >= 3) ||
                          (ratingFilter === '0' && book.rating === 0)
    
    return matchesSearch && matchesShelf && matchesStatus && matchesRating
  })

  const handleAddBook = (bookData: Omit<Book, 'id'>) => {
    const newBook = {
      ...bookData,
      id: Date.now().toString()
    }
    setBooks([...books, newBook])
    setIsModalOpen(false)
  }

  const handleEditBook = (bookData: Omit<Book, 'id'>) => {
    if (editingBook) {
      setBooks(books.map(book => 
        book.id === editingBook.id ? { ...bookData, id: editingBook.id } : book
      ))
      setEditingBook(null)
      setIsModalOpen(false)
    }
  }

  const openEditModal = (book: Book) => {
    setEditingBook(book)
    setIsModalOpen(true)
  }

  const handleAddShelf = (name: string) => {
    const newShelf = { id: Date.now().toString(), name }
    setShelves([...shelves, newShelf])
  }

  return (
    <div className="min-h-screen bg-background">
      <Header />
      
      <div className="flex">
        <Sidebar 
          shelves={shelves}
          selectedShelf={selectedShelf}
          onShelfSelect={setSelectedShelf}
          onAddShelf={handleAddShelf}
        />
        
        <main className="flex-1 p-6 ml-64 pt-20">
          <div className="max-w-7xl mx-auto">
            <div className="flex justify-between items-center mb-6">
              <h1 className="text-3xl font-bold text-foreground">Meus Livros</h1>
              <Button 
                onClick={() => {
                  setEditingBook(null)
                  setIsModalOpen(true)
                }}
                className="bg-teal-600 hover:bg-teal-700 text-white"
              >
                Adicionar Livro
              </Button>
            </div>

            {/* Filtros */}
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-6">
              <Input
                type="text"
                placeholder="Pesquisar por título ou autor..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="bg-background"
              />

              <Select value={statusFilter} onValueChange={setStatusFilter}>
                <SelectTrigger>
                  <SelectValue placeholder="Filtrar por status" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">Todos os status</SelectItem>
                  <SelectItem value="lendo">Lendo</SelectItem>
                  <SelectItem value="lido">Lido</SelectItem>
                  <SelectItem value="a-ler">A Ler</SelectItem>
                </SelectContent>
              </Select>

              <Select value={ratingFilter} onValueChange={setRatingFilter}>
                <SelectTrigger>
                  <SelectValue placeholder="Filtrar por avaliação" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">Todas as avaliações</SelectItem>
                  <SelectItem value="4+">4+ estrelas</SelectItem>
                  <SelectItem value="3+">3+ estrelas</SelectItem>
                  <SelectItem value="0">Sem avaliação</SelectItem>
                </SelectContent>
              </Select>

              <Button 
                variant="outline" 
                onClick={() => {
                  setSearchTerm('')
                  setStatusFilter('all')
                  setRatingFilter('all')
                  setSelectedShelf('all')
                }}
              >
                Limpar Filtros
              </Button>
            </div>

            {/* Estatísticas rápidas */}
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-6">
              <div className="bg-card border border-border rounded-lg p-4 text-center">
                <div className="text-2xl font-bold text-foreground">{books.length}</div>
                <div className="text-sm text-muted-foreground">Total</div>
              </div>
              <div className="bg-card border border-border rounded-lg p-4 text-center">
                <div className="text-2xl font-bold text-green-600">
                  {books.filter(b => b.status === 'lido').length}
                </div>
                <div className="text-sm text-muted-foreground">Lidos</div>
              </div>
              <div className="bg-card border border-border rounded-lg p-4 text-center">
                <div className="text-2xl font-bold text-blue-600">
                  {books.filter(b => b.status === 'lendo').length}
                </div>
                <div className="text-sm text-muted-foreground">Lendo</div>
              </div>
              <div className="bg-card border border-border rounded-lg p-4 text-center">
                <div className="text-2xl font-bold text-gray-600">
                  {books.filter(b => b.status === 'a-ler').length}
                </div>
                <div className="text-sm text-muted-foreground">A Ler</div>
              </div>
            </div>

            {/* Lista de livros */}
            {filteredBooks.length === 0 ? (
              <div className="text-center py-12">
                <p className="text-muted-foreground text-lg">
                  {books.length === 0 
                    ? 'Sua biblioteca está vazia. Adicione seu primeiro livro!' 
                    : 'Nenhum livro encontrado com os filtros aplicados.'
                  }
                </p>
              </div>
            ) : (
              <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-6">
                {filteredBooks.map((book) => (
                  <BookCard 
                    key={book.id} 
                    book={book} 
                    onEdit={() => openEditModal(book)}
                  />
                ))}
              </div>
            )}
          </div>
        </main>
      </div>

      <BookFormModal
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false)
          setEditingBook(null)
        }}
        onSubmit={editingBook ? handleEditBook : handleAddBook}
        book={editingBook}
        shelves={shelves}
      />
    </div>
  )
}
