"use client"

import { Button } from '@/components/ui/button'
import { Card, CardContent } from '@/components/ui/card'

interface Book {
  id: string
  title: string
  author: string
  cover: string
  synopsis: string
  status: 'lendo' | 'lido' | 'a-ler'
  rating: number
  shelf: string
}

interface BookCardProps {
  book: Book
  onEdit: () => void
}

export default function BookCard({ book, onEdit }: BookCardProps) {
  const getStatusColor = (status: string) => {
    switch (status) {
      case 'lido':
        return 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200'
      case 'lendo':
        return 'bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200'
      case 'a-ler':
        return 'bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-200'
      default:
        return 'bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-200'
    }
  }

  const getStatusText = (status: string) => {
    switch (status) {
      case 'lido':
        return 'Lido'
      case 'lendo':
        return 'Lendo'
      case 'a-ler':
        return 'A Ler'
      default:
        return status
    }
  }

  const renderStars = (rating: number) => {
    return Array.from({ length: 5 }, (_, i) => (
      <span
        key={i}
        className={`text-lg ${
          i < rating ? 'text-yellow-400' : 'text-gray-300 dark:text-gray-600'
        }`}
      >
        ★
      </span>
    ))
  }

  return (
    <Card className="group hover:shadow-lg transition-all duration-200 cursor-pointer">
      <CardContent className="p-0">
        <div className="relative">
          <img
            src={book.cover || "https://placehold.co/400x600?text=Capa+do+Livro+Placeholder+design+minimalista+e+moderno"}
            alt="Capa do livro em design minimalista com cores suaves"
            className="w-full h-64 object-cover rounded-t-lg"
            onError={(e) => {
              const target = e.target as HTMLImageElement;
              target.src = "https://placehold.co/400x600?text=Sem+Capa";
            }}
          />
          <div className="absolute top-2 right-2">
            <span className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(book.status)}`}>
              {getStatusText(book.status)}
            </span>
          </div>
        </div>
        
        <div className="p-4">
          <h3 className="font-semibold text-foreground text-sm mb-1 line-clamp-2 leading-tight">
            {book.title}
          </h3>
          <p className="text-muted-foreground text-xs mb-3 line-clamp-1">
            {book.author}
          </p>
          
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-1">
              {renderStars(book.rating)}
            </div>
            <Button
              variant="ghost"
              size="sm"
              onClick={onEdit}
              className="opacity-0 group-hover:opacity-100 transition-opacity text-xs px-2 py-1 h-auto"
            >
              Editar
            </Button>
          </div>
        </div>
      </CardContent>
    </Card>
  )
}
