"use client"

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog'

interface Book {
  id: string
  title: string
  author: string
  cover: string
  synopsis: string
  status: 'lendo' | 'lido' | 'a-ler'
  rating: number
  shelf: string
}

interface Shelf {
  id: string
  name: string
}

interface BookFormModalProps {
  isOpen: boolean
  onClose: () => void
  onSubmit: (book: Omit<Book, 'id'>) => void
  book?: Book | null
  shelves: Shelf[]
}

export default function BookFormModal({ isOpen, onClose, onSubmit, book, shelves }: BookFormModalProps) {
  const [formData, setFormData] = useState({
    title: '',
    author: '',
    cover: '',
    synopsis: '',
    status: 'a-ler' as 'lendo' | 'lido' | 'a-ler',
    rating: 0,
    shelf: ''
  })

  useEffect(() => {
    if (book) {
      setFormData({
        title: book.title,
        author: book.author,
        cover: book.cover,
        synopsis: book.synopsis,
        status: book.status,
        rating: book.rating,
        shelf: book.shelf
      })
    } else {
      setFormData({
        title: '',
        author: '',
        cover: '',
        synopsis: '',
        status: 'a-ler',
        rating: 0,
        shelf: shelves[0]?.id || ''
      })
    }
  }, [book, shelves, isOpen])

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    if (formData.title && formData.author) {
      onSubmit(formData)
    }
  }

  const handleRatingClick = (rating: number) => {
    setFormData({ ...formData, rating })
  }

  const renderStars = () => {
    return Array.from({ length: 5 }, (_, i) => (
      <button
        key={i}
        type="button"
        onClick={() => handleRatingClick(i + 1)}
        className={`text-2xl transition-colors ${
          i < formData.rating ? 'text-yellow-400' : 'text-gray-300 dark:text-gray-600 hover:text-yellow-300'
        }`}
      >
        ★
      </button>
    ))
  }

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-md max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>{book ? 'Editar Livro' : 'Adicionar Novo Livro'}</DialogTitle>
        </DialogHeader>
        
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <Label htmlFor="title">Título *</Label>
            <Input
              id="title"
              value={formData.title}
              onChange={(e) => setFormData({ ...formData, title: e.target.value })}
              placeholder="Digite o título do livro"
              required
            />
          </div>

          <div>
            <Label htmlFor="author">Autor *</Label>
            <Input
              id="author"
              value={formData.author}
              onChange={(e) => setFormData({ ...formData, author: e.target.value })}
              placeholder="Digite o nome do autor"
              required
            />
          </div>

          <div>
            <Label htmlFor="cover">URL da Capa</Label>
            <Input
              id="cover"
              value={formData.cover}
              onChange={(e) => setFormData({ ...formData, cover: e.target.value })}
              placeholder="https://exemplo.com/capa.jpg"
            />
          </div>

          <div>
            <Label htmlFor="synopsis">Sinopse</Label>
            <Textarea
              id="synopsis"
              value={formData.synopsis}
              onChange={(e) => setFormData({ ...formData, synopsis: e.target.value })}
              placeholder="Digite uma breve sinopse do livro"
              rows={3}
            />
          </div>

          <div>
            <Label htmlFor="shelf">Estante</Label>
            <Select value={formData.shelf} onValueChange={(value) => setFormData({ ...formData, shelf: value })}>
              <SelectTrigger>
                <SelectValue placeholder="Selecione uma estante" />
              </SelectTrigger>
              <SelectContent>
                {shelves.map((shelf) => (
                  <SelectItem key={shelf.id} value={shelf.id}>
                    {shelf.name}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          <div>
            <Label htmlFor="status">Status de Leitura</Label>
            <Select value={formData.status} onValueChange={(value: 'lendo' | 'lido' | 'a-ler') => setFormData({ ...formData, status: value })}>
              <SelectTrigger>
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="a-ler">A Ler</SelectItem>
                <SelectItem value="lendo">Lendo</SelectItem>
                <SelectItem value="lido">Lido</SelectItem>
              </SelectContent>
            </Select>
          </div>

          <div>
            <Label>Avaliação</Label>
            <div className="flex items-center space-x-1 mt-2">
              {renderStars()}
              <span className="ml-2 text-sm text-muted-foreground">
                {formData.rating > 0 ? `${formData.rating} estrela${formData.rating > 1 ? 's' : ''}` : 'Sem avaliação'}
              </span>
            </div>
          </div>

          <div className="flex justify-end space-x-2 pt-4">
            <Button type="button" variant="outline" onClick={onClose}>
              Cancelar
            </Button>
            <Button 
              type="submit" 
              className="bg-teal-600 hover:bg-teal-700 text-white"
              disabled={!formData.title || !formData.author}
            >
              {book ? 'Salvar Alterações' : 'Adicionar Livro'}
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  )
}
