"use client"

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'

interface Shelf {
  id: string
  name: string
}

interface SidebarProps {
  shelves: Shelf[]
  selectedShelf: string
  onShelfSelect: (shelfId: string) => void
  onAddShelf: (name: string) => void
}

export default function Sidebar({ shelves, selectedShelf, onShelfSelect, onAddShelf }: SidebarProps) {
  const [newShelfName, setNewShelfName] = useState('')
  const [isDialogOpen, setIsDialogOpen] = useState(false)

  const handleAddShelf = () => {
    if (newShelfName.trim()) {
      onAddShelf(newShelfName.trim())
      setNewShelfName('')
      setIsDialogOpen(false)
    }
  }

  return (
    <aside className="fixed left-0 top-16 h-[calc(100vh-4rem)] w-64 bg-background border-r border-border overflow-y-auto">
      <div className="p-4">
        <div className="mb-6">
          <h2 className="text-lg font-semibold text-foreground mb-4">Estantes</h2>
          
          <div className="space-y-2">
            <button
              onClick={() => onShelfSelect('all')}
              className={`w-full text-left px-3 py-2 rounded-lg transition-colors ${
                selectedShelf === 'all'
                  ? 'bg-teal-100 text-teal-800 dark:bg-teal-900 dark:text-teal-200'
                  : 'hover:bg-muted text-foreground'
              }`}
            >
              Todos os Livros
            </button>
            
            {shelves.map((shelf) => (
              <button
                key={shelf.id}
                onClick={() => onShelfSelect(shelf.id)}
                className={`w-full text-left px-3 py-2 rounded-lg transition-colors ${
                  selectedShelf === shelf.id
                    ? 'bg-teal-100 text-teal-800 dark:bg-teal-900 dark:text-teal-200'
                    : 'hover:bg-muted text-foreground'
                }`}
              >
                {shelf.name}
              </button>
            ))}
          </div>
        </div>

        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button 
              variant="outline" 
              className="w-full border-dashed border-2 border-muted-foreground/25 hover:border-teal-500 hover:bg-teal-50 dark:hover:bg-teal-950"
            >
              + Nova Estante
            </Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Criar Nova Estante</DialogTitle>
            </DialogHeader>
            <div className="space-y-4">
              <Input
                placeholder="Nome da estante"
                value={newShelfName}
                onChange={(e) => setNewShelfName(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && handleAddShelf()}
              />
              <div className="flex justify-end space-x-2">
                <Button variant="outline" onClick={() => setIsDialogOpen(false)}>
                  Cancelar
                </Button>
                <Button 
                  onClick={handleAddShelf}
                  className="bg-teal-600 hover:bg-teal-700 text-white"
                >
                  Criar
                </Button>
              </div>
            </div>
          </DialogContent>
        </Dialog>

        <div className="mt-8 pt-6 border-t border-border">
          <h3 className="text-sm font-medium text-muted-foreground mb-3">Estatísticas</h3>
          <div className="space-y-2 text-sm">
            <div className="flex justify-between">
              <span className="text-muted-foreground">Livros lidos:</span>
              <span className="font-medium text-foreground">0</span>
            </div>
            <div className="flex justify-between">
              <span className="text-muted-foreground">Meta anual:</span>
              <span className="font-medium text-foreground">12</span>
            </div>
            <div className="flex justify-between">
              <span className="text-muted-foreground">Progresso:</span>
              <span className="font-medium text-teal-600">0%</span>
            </div>
          </div>
        </div>
      </div>
    </aside>
  )
}
