"use client"

import { useTheme } from 'next-themes'
import Link from 'next/link'
import { usePathname } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar'

export default function Header() {
  const { theme, setTheme } = useTheme()
  const pathname = usePathname()

  const navItems = [
    { href: '/', label: 'Biblioteca' },
    { href: '/dashboard', label: 'Dashboard' },
    { href: '/books', label: 'Meus Livros' }
  ]

  return (
    <header className="fixed top-0 left-0 right-0 z-50 bg-background border-b border-border">
      <div className="flex items-center justify-between px-6 py-4">
        <div className="flex items-center space-x-6">
          <h1 className="text-xl font-bold text-foreground">Biblioteca Virtual</h1>
          
          <nav className="hidden md:flex items-center space-x-4">
            {navItems.map((item) => (
              <Link
                key={item.href}
                href={item.href}
                className={`text-sm transition-colors hover:text-teal-600 ${
                  pathname === item.href
                    ? 'text-teal-600 font-medium'
                    : 'text-muted-foreground'
                }`}
              >
                {item.label}
              </Link>
            ))}
          </nav>
        </div>
        
        <div className="flex items-center space-x-4">
          <Button
            variant="outline"
            size="sm"
            onClick={() => setTheme(theme === 'dark' ? 'light' : 'dark')}
            className="text-sm"
          >
            {theme === 'dark' ? 'Modo Claro' : 'Modo Escuro'}
          </Button>
          
          <div className="flex items-center space-x-3">
            <Avatar className="h-8 w-8">
              <AvatarImage 
                src="https://placehold.co/80x80?text=User+Profile+Placeholder+minimalista+em+fundo+neutro" 
                alt="Imagem de perfil do usuário com design minimalista e fundo neutro"
                onError={(e) => {
                  const target = e.target as HTMLImageElement;
                  target.src = "https://placehold.co/80x80?text=U";
                }}
              />
              <AvatarFallback>U</AvatarFallback>
            </Avatar>
            <div className="text-sm">
              <p className="font-medium text-foreground">Usuário</p>
              <p className="text-xs text-muted-foreground">Leitor ativo</p>
            </div>
          </div>
        </div>
      </div>
    </header>
  )
}
